import { parse } from 'qs'
let inNode = typeof window === '' + void 0

/**
 *è§£æURL
 *params: 
	url: éè¦è§£æçå°å
 *returnï¼
 	ä¸ä¸ªé¿åº¦å¯¹è±¡
 *å¶ä¸­schemeè¡¨ç¤ºåè®®å¤´ï¼authorityè¡¨ç¤ºååï¼pathè¡¨ç¤ºè·¯å¾ï¼queryè¡¨ç¤ºurlä¸­ï¼å#ä¹åçé¨åï¼fragmentè¡¨ç¤º#åé¢çé¨å
 */

export const url_parse = (url = window.location.href) => {
	const reg = /^(([^:/?#]+):)?(\/\/([^/?#]*))?([^?#]*)(\?([^#]*))?(#(.*))?/

	const arr = url.match(reg)

	return {
		scheme: arr[2],
		authority: arr[4],
		path: arr[5],
		query: parse(arr[7]),
		fragment: arr[9]
	}
}

/**
 *è·åurlä¸­nameçå¼
 *params: 
 	name: éè¦è§£æçqueryä¸­çå­æ®µ
	url: éè¦è§£æçå°å
 *return:
 	val: urlä¸­nameçå¼
 */

export const get_query_by_name = (name, req) => {
	if (inNode && req) {
		return req.query[name] || undefined
	} else {
		const url = window.location.href
		const query = url_parse(url).query
		const val = query[name] || undefined
		return val
	}
}
