import axios from 'axios'
import CryptoJS from 'crypto-js'
import {
	getSymmetryPassword,
	getUuid,
	getUid,
	getCookie
} from './util'

/**
 *
 * @param {Function} method è¯·æ±æ¹æ³
 * @returns
 */
const http_secret_zcm = (obj) => {
	const {
		url = '',
			params = {},
			req = null,
			g_config = {}
	} = obj
	/* 
	  	å¤çè¯·æ±åæ°
	 */
	let config = {}
	let symmetryPassword = ''
	try {
		// è®¾ç½®headerä¿¡æ¯
		const zpVersion = getCookie('zp-version', req) || '0.0.0'
		config = {
			contentType: 'form',
			headers: {
				'zp-version': zpVersion,
				'Content-Type': 'application/x-www-form-urlencoded'
			},
		}
		// å¦ææ¯æå¡ç«¯æ¸²æï¼å°nodeæå¡æ¥æ¶çrequestå¯¹è±¡ä¸­çcookieæºå¸¦
		if (typeof window === 'undefined' && req) {
			config.headers = {
				...config.headers,
				Cookie: req.headers.cookie
			}
		}
		config.withCredentials = true

		// è·åUUID
		const uuid = getUuid()
		// è·åå¯¹ç§°å å¯å¯é¥
		symmetryPassword = getSymmetryPassword()

		// å å¯è¿ç¨
		const {
			cmd,
			...others
		} = params || {}
		// å¯¹hbodyå å¯
		let hbody =
			Object.keys(others).length == 0 ?
			JSON.stringify({
				uuid
			}) :
			JSON.stringify(others)
		hbody = CryptoJS.AES.encrypt(
			hbody,
			CryptoJS.enc.Utf8.parse(symmetryPassword), {
				mode: CryptoJS.mode.ECB,
				padding: CryptoJS.pad.Pkcs7
			}
		).toString()

		// éè¿å¯¹ä¸ä¸ªç±uuid,hbody,cmd,key,indexç­åæ°ç»æçå­ç¬¦ä¸²è¿è¡æè¦ï¼çæç­¾å
		const sign = CryptoJS.MD5(
			`${params.cmd}&hbody=${hbody}&uuid=${uuid}&index=h5&h5`
		).toString()

		// ä¸¤æ¬¡encodeè§å
		hbody = encodeURIComponent(hbody)
		hbody = encodeURIComponent(hbody)
		const requestBody = {
			hbody,
			uuid,
			sign,
			cmd,
			key: 'h5',
			index: 'h5'
		}

		if (getUid(req).length > 0) {
			requestBody.uid = getUid(req)
		}

		let formData = ''
		for (let key in requestBody) {
			formData += `${key}=${requestBody[key]}&`
		}

		config = {
			...config,
			data: formData,
			method: 'post',
			url,
			...g_config
		}
	} catch (error) {
		console.error('åæ°å¤çå¼å¸¸:', error)
		throw error
	}

	/*
		è¯·æ±
	*/
	return new Promise((resolve, reject) => {
		try {
			axios(config).then((res) => {
					//  è§£å¯è¿ç¨
					let {
						result = '',
							status = -1
					} = res.data || {}
					if (typeof result == 'string') {
						result = CryptoJS.AES.decrypt(
							result,
							CryptoJS.enc.Utf8.parse(symmetryPassword), {
								mode: CryptoJS.mode.ECB,
								padding: CryptoJS.pad.Pkcs7
							}
						).toString(CryptoJS.enc.Utf8)

						if (result === '') {
							res.data.result = {}
						} else {
							res.data.result = JSON.parse(result)
						}
					}
					resolve(res.data || {})
				})
				.catch((err) => {
					reject(err.data)
				})

		} catch (error) {
			console.error('JSå¼å¸¸:', error)
			reject(error)
		}
	})
}

export default {
	http_secret_zcm,
	getUid
}