// è·åcookie
export const  getCookie = (key) => {
	if (key === undefined) return null
	let mReg = new RegExp('(^|;)\\s*' + key + '=([^;]*)(;|$)')
	let mValue = document.cookie.match(mReg)
	let ret = undefined

	if (mValue != null) {
		ret = unescape(mValue[2])
		if (ret !== undefined) {
			ret = ret.replace(/^\"|\'/i, '').replace(/\"|\'$/i, '') // è¿æ»¤å­ç¬¦ä¸²æå¤å±å¼å·
		}
	}
	return ret
}
